<template>
  <div id="article"  v-if="this.data != undefined">
    <div class="article-header" >
      <div class="article-header-user">
        <img class="article-header-userIcon" :src="data.authorImage"/>
        <span class="article-header-userName">{{data.author}}</span>
      </div>
    </div>
    <div class="article-content">
      <div class="article-content-title">{{data.name}}</div>
      <div v-for="item in data.items" class="article-content-cell">

        <div v-if="item.type == 1" >
          <img class="article-content-cellImg" :src="item.image"/>
          <div v-if="item.text.length > 0" class="article-content-cell1Label">{{item.text}}</div>
        </div>
        <div v-else-if="item.type == 2">
          <video class="article-content-cell2Video" :src="item.videoUrl" controls="controls"></video>
          <div v-if="item.text.length > 0" class="article-content-cell1Label">{{item.text}}</div>
        </div>
        <div v-else-if="item.type == 0">
          <div v-if="item.text.length > 0" class="article-content-cell0Label">{{item.text}}</div>
        </div>

      </div>
    </div>
    <!--<share-footer title="下载参与留言"></share-footer>-->
    <messages class="article-messages" type="0" :id="id"></messages>
  </div>
</template>

<script>
  import messages from './messages.vue'
  import api from '@/api/common/api.common'
  import shareFooter from '../share/shareFooter.vue'
  export default {
    name: 'article',
    props: ['id'],
    mounted () {
      var self = this
      var unionid = localStorage.getItem('user-unionid-cache')

      if (unionid) {
        api.getArticleDetail(this.id,function (respond) {
          self.data = respond.body.data
        })
      }

    },
    methods: {

    },
    components: {
      shareFooter,
      messages
    },
    computed: {

    },
    data () {
      return {
        // ../../assets/icon/userIcon.png
        authorImage: '../../assets/icon/userIcon.png',
        data: undefined

      }
    }
  }
</script>



<style lang="scss">
  @import '../../assets/css/mixin';
  #article {
    background-color: #eeeeee;
  }

  .article-header {
    margin-top: pxToRem(10px);
    background-color: white;
    padding-top: pxToRem(30px);
    padding-bottom: pxToRem(50px);
  }
  .article-header-user {
    height: pxToRem(30px);
    padding-left: pxToRem(20px);
    display: flex;
    align-items: center;
  }

  .article-header-userIcon {
    width: pxToRem(30px);
    height: pxToRem(30px);
  }

  .article-header-userName {
    font-family: PingFangSC-Light;
    font-size: pxToRem(12px);
    color:#666666;
    letter-spacing:0;
    text-align:left;
    margin-left: pxToRem(4px);
  }

  .article-content {
    background-color: white;
  }
  .article-content-title {
    font-family: PingFangSC-Regular;
    font-size: pxToRem(22px);
    color:#544014;
    letter-spacing:pxToRem(1px);
    text-align:left;
    padding-left: pxToRem(33px);
    padding-bottom: pxToRem(30px);
  }
  .article-content-cell {
    padding-bottom: pxToRem(40px);
  }

  .article-content-cellImg {
    margin-left: pxToRem(16px);
    margin-right: pxToRem(16px);
    width: 91.4666666%;
    border-radius: pxToRem(5px);
    height: auto;
  }

  .article-content-cell1Label {
    color: #BDBDBD;
    padding-left: pxToRem(33px);
    padding-right: pxToRem(33px);
    padding-top: pxToRem(8px);
    font-size: pxToRem(12px);
    letter-spacing:pxToRem(0.71px);

  }

  .article-content-cell2Video {
    background:rgba(84,64,20,0.30);
    margin-left: pxToRem(16px);
    margin-right: pxToRem(16px);
    width: 91.4666666%;
    border-radius: pxToRem(5px);
    height:pxToRem(250px);
  }

  .article-content-cell0Label {
    font-family: PingFangSC-Light;
    font-size: pxToRem(13px);
    color:#544014;
    letter-spacing: pxToRem(2.71px);
    line-height: pxToRem(26px);
    text-align:left;
    padding-left: pxToRem(30px);
    padding-right: pxToRem(30px);
  }

</style>



