<template>
  <div id="course">

    <div class="course-header">
      <img v-if="data && data.detailImage.length > 0" class="course-header-img" :src="data.detailImage"/>
      <div class="course-header-author">
        <div class="course-header-author-content">
          <img class="course-header-author-icon" :src="data.authorImage"/>
          <div class="course-header-author-name">{{data.author}}</div>
        </div>
      </div>
      <div class="course-header-bottom">
        <!--<img class="course-header-bottom-img" src="../../assets/icon/share.png"/>-->
        <!--<img class="course-header-bottom-img" src="../../assets/icon/like.png"/>-->
        <!--<img class="course-header-bottom-img" src="../../assets/icon/good.png"/>-->
      </div>
    </div>

    <div class="course-flavour">
      <div class="course-flavour-label">用料</div>
      <div class="course-flavour-content">
        <div class="course-flavour-cell" v-for="materials in data.materials">
          <span class="course-flavour-cellLabel">{{materials.material}}</span>
          <span class="course-flavour-cellLabel">{{materials.quantity}}{{materials.specifications}}</span>
        </div>
      </div>
    </div>

    <div class="course-step">

      <div v-for="(item,index) in data.items">
        <div class="course-step-header">烹饪步骤{{index+1}}/{{data.items.length}}</div>

        <div class="course-step-content1">
          <img class="course-step-content1-img" :src="item.image"/>
          <div class="course-step-content1-text1">

            <img class="course-step-text1-img" src="../../assets/icon/info.png"/>
            <span class="course-step-text1-label">{{item.materials}}</span>

          </div>
          <div class="course-step-content1-text2">
            <img class="course-step-text2-img" src="../../assets/icon/tool.png"/>
            <span class="course-step-text2-label">{{item.tools}}</span>
          </div>
        </div>
        <hr style="height: 1px;background-color: #cccccc;"/>

        <div class="course-step-content2">{{item.description}}</div>
        <hr style="height: 1px;background-color: #cccccc;"/>
      </div>

    </div>

    <div class="course-afterClass">

      <div class="course-afterClass-header">
        课后互动
      </div>

      <div class="course-afterClass-content">
        <span class="course-afterClass-cell" v-for="task in data.tasks">

          <img class="course-afterClass-cell-img" :src="task.imgUrl"/>

          <div class="course-afterClass-cell-view1">

            <span class="course-afterClass-cell-user">
              <img class="course-afterClass-cell-userIcon" :src="task.imgUrl"/>

              <div class="course-afterClass-cell-userName">{{task.userStr}}</div>
            </span>

            <span class="course-afterClass-cell-rightView" @click="likeEvent(task.id,task,4)">
              <div class="course-afterClass-cell-label1">{{task.feeling}}</div>
              <img v-if="task.isLike" class="course-afterClass-cell-goodImg" src="../../assets/icon/like2.png"/>
              <img v-else="" class="course-afterClass-cell-goodImg" src="../../assets/icon/good.png"/>
            </span>

          </div>
        </span>
      </div>

    </div>

    <!--<div class="course-footer">-->
      <!---->
    <!--</div>-->
    <shareFooter title="下载后分享你的作业"></shareFooter>

    <messages class="course-messages" type="1" :id="id"></messages>
    <share-footer v-if="data && data.likeCount === 0" title="下载参与留言"></share-footer>
    <shareFooter v-else="" :title="'下载参与留言  热议'+data.likeCount+'条'"></shareFooter>
  </div>
</template>

<script>
  import messages from './messages.vue'
  import api from '@/api/common/api.common'
  import shareFooter from '../share/shareFooter.vue'
  export default {
    name: 'course',
    props: ['id'],
    components: {
      messages,
      shareFooter
    },
    mounted () {
      var self = this
      var unionid = localStorage.getItem('user-unionid-cache')

      if (unionid) {
        api.getCourseDetail(this.id,function (respond) {
          self.data = respond.body.data
        })
      }

    },
    methods: {
      likeEvent (id,task,type) {
        var unionid = localStorage.getItem('user-unionid-cache')
        var self = this
        var data = {}
        data.likeId = id
        data.source = type
        data.platform = 1
        data.platformUser = unionid
        api.setLikeMassage(data,function (respond) {
          task.isLike = true
        })
      }
    },
    computed: {

    },
    data () {
      return {
        imgURL: undefined,
        authorName: 'tcjxm6',
        data: {}
      }
    }
  }
</script>


<style lang="scss">
  @import '../../assets/css/mixin';
  #course {
    background-color: #eeeeee;
  }

  .course-header {
    background: #fff;
    margin-bottom: pxToRem(10px);
  }
  .course-header-img {
    width: 100%;
    height: pxToRem(343px);
  }

  .course-header-author {
    height: pxToRem(140px);
    display: flex;
    justify-content: center;
    align-items: center;

  }

  .course-header-bottom {
    height: pxToRem(59px);
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding-left: pxToRem(34px);
    padding-right: pxToRem(34px);
    /*background-color: #4caf50;*/
  }

  .course-header-bottom-img {
    width: pxToRem(20px);
    height: auto;
  }

  .course-header-author-content {
    text-align: center;
  }
  .course-header-author-icon {
    margin-top: pxToRem(40px);
    width: pxToRem(30px);
    height: pxToRem(30px);
    border-radius: pxToRem(15px);
  }

  .course-header-author-name {
    font-size: pxToRem(12px);
    margin-top: pxToRem(11px);
    color: #9b9b9b;
  }

  .course-flavour {
    background-color: white;
    padding-top: pxToRem(40px);
    margin-bottom: pxToRem(10px);
    text-align: center;

    .course-flavour-label {
      font-size: pxToRem(14px);
      font-family: PingFangSC-Regular;
      color: #544014;
    }

    .course-flavour-content {
      margin-top: pxToRem(30px);
      padding-bottom: pxToRem(20px);
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      padding-left: pxToRem(63px);
      padding-right: pxToRem(63px);

      .course-flavour-cell {

        display: flex;
        justify-content: space-between;
        margin-bottom: pxToRem(20px);

        .course-flavour-cellLabel {
          color: #544014;
          font-family: PingFangSC-Light;
          font-size: pxToRem(12px);
        }

      }

    }

  }

  .course-step {
    background-color: white;
    margin-bottom: pxToRem(10px);

    .course-step-header {
      padding-top: pxToRem(40px);
      padding-bottom: pxToRem(20px);
      color: #544014;
      font-size: pxToRem(14px);
      text-align: center;
    }

    .course-step-content1 {

      .course-step-content1-img {
        /*height:pxToRem(180px);*/
        height: auto;
        width: 91.466666%;
        border-radius: pxToRem(5px);
        background-color: white;
        margin-left: pxToRem(16px);
        margin-right: pxToRem(16px);
      }

      .course-step-content1-text1 {
        margin-top: pxToRem(17px);
        padding-left: pxToRem(33px);
        padding-right: pxToRem(16px);
        display: flex;
        justify-content: space-between;
        align-items: flex-start;

        .course-step-text1-img {
          width: pxToRem(15px);
          padding-top: pxToRem(4px);
        }
        .course-step-text1-label {
          margin-left: pxToRem(9px);
          width: pxToRem(300px);
          font-size: pxToRem(14px);
          letter-spacing:1px;
          line-height:pxToRem(23px);
          font-family: PingFangSC-Light;
          color: #544014;
        }
      }

      .course-step-content1-text2 {
        margin-top: pxToRem(17px);
        padding-left: pxToRem(33px);
        padding-right: pxToRem(16px);
        padding-bottom: pxToRem(18px);
        display: flex;
        justify-content: space-between;
        align-items: center;

        .course-step-text2-img {
          width: pxToRem(15px);
          padding-top: pxToRem(2px);
        }
        .course-step-text2-label {
          margin-left: pxToRem(9px);
          padding-top: pxToRem(3px);
          width: pxToRem(300px);
          font-size: pxToRem(14px);
          letter-spacing:1px;
          line-height:pxToRem(23px);
          font-family: PingFangSC-Light;
          color: #544014;
        }
      }
    }

    .course-step-content2 {
      padding: pxToRem(19px) pxToRem(33px);
      font-size: pxToRem(14px);
      line-height: pxToRem(26px);
      color: #544014;
      font-family: PingFangSC-Light;
      letter-spacing:pxToRem(3px);
    }

  }

  .course-afterClass {


    background-color: white;
    height: pxToRem(410px);

    .course-afterClass-header {
      color: #544014;
      font-size: pxToRem(14px);
      text-align: center;
      padding-top: pxToRem(40px);
      padding-bottom: pxToRem(30px);
    }

    .course-afterClass-content {
      height: pxToRem(320px);
      overflow-x: auto;
      white-space: nowrap;
      padding-left: (25px);

      .course-afterClass-cell {
        display: inline-block;
        white-space: normal;
        border:.5px solid #979797;
        width: pxToRem(250px);
        height: pxToRem(317px);
        background-color: white;
        border-top-left-radius: pxToRem(5px);
        border-top-right-radius: pxToRem(5px);
        margin-right: pxToRem(20px);
        overflow: hidden;

        .course-afterClass-cell-img {
          width: pxToRem(250px);
          height: pxToRem(220px);

        }
        .course-afterClass-cell-view1 {
          width: pxToRem(250px);
          height: pxToRem(97px);
          display: flex;
          justify-content: space-between;
          .course-afterClass-cell-user {
            width: pxToRem(87px);
            height: pxToRem(97px);

            display: inline-block;

            .course-afterClass-cell-userIcon {
              margin-top: pxToRem(20px);
              margin-left: pxToRem(28px);
              width: pxToRem(30px);
              height: pxToRem(30px);
              border-radius: pxToRem(10px);
            }
            .course-afterClass-cell-userName {
              margin-top: pxToRem(10px);
              margin-left: pxToRem(13px);
              margin-right: pxToRem(13px);
              text-align: center;
              color: #9B9B9B;
              text-overflow: ellipsis;
              overflow: hidden;
              white-space:nowrap;
              font-size: pxToRem(12px);
              font-family: PingFangSC-Light;
            }
          }
          .course-afterClass-cell-rightView {
            width: pxToRem(250-87px);
            height: pxToRem(97px);
            display: inline-block;
            text-align: right;
            .course-afterClass-cell-label1 {
              text-align: left;
              margin-top: pxToRem(20px);
              margin-left: pxToRem(10px);
              /*margin-right: pxToRem(10px);*/
              height: pxToRem(34px);
              font-size: pxToRem(12px);
              color: #544014;
              overflow : hidden; text-overflow: ellipsis; display: -webkit-box; -webkit-line-clamp: 2; -webkit-box-orient: vertical;
            }
            .course-afterClass-cell-goodImg {
              width: pxToRem(20px);
              height: pxToRem(20px);
              margin-top: pxToRem(3px);
              margin-right: pxToRem(10px);
            }
          }
        }


      }

    }


  }

  .course-messages {
    margin-top: pxToRem(10px);
  }
</style>



