<template>
  <div id="messages">
    <div class="messages-header">
      留言列表
    </div>

    <div class="messages-cell" v-for="(msg,index) in datas">
      <div class="messages-cell-view1">
        <span style="display: flex; align-items: center">
          <img class="messages-cell-userIcon" :src="msg.leaveMessageUserHeadImg"/>
          <span class="messages-cell-userName" :value="msg.leaveMessageUserName">{{msg.leaveMessageUserName}}</span>
        </span>

        <span class="messages-cell-time">{{msg.reportTimeStr}}</span>
      </div>
      <div :class="'messages-cell-view2 messages-cell-labelWrap messages-cell-view2'+index" ref="cell_label1">
        {{msg.message}}
      </div>
      <div class="messages-cell-view3">
        <span :class="'messages-cell-label '+ 'messages-cell-label'+index" @click="onwind(index)">展开</span>
        <span class="messages-cell-iconView1">
          <span class="messages-cell-iconView">
            <img class="messages-cell-iconView-img" src="../../assets/icon/report.png"/>
            <span class="messages-cell-iconView-label">举报</span>
          </span>
          <span class="messages-cell-iconView">
            <img class="messages-cell-iconView-img" src="../../assets/icon/message.png"/>
            <span class="messages-cell-iconView-label">评论</span>
          </span>
          <span class="messages-cell-iconView messages-cell-iconView3" @click="likeEvent(msg.id,msg)">
            <img v-if="msg.isLike" class="messages-cell-iconView-img" src="../../assets/icon/like2.png"/>
            <img v-else="" class="messages-cell-iconView-img" src="../../assets/icon/good.png"/>
            <span class="messages-cell-iconView-label">赞</span>
          </span>
        </span>
      </div>
      <hr style="height: 1px;background-color: #cccccc;"/>
    </div>


  </div>
</template>

<script>

  import api from '@/api/common/api.common'
  export default {
    name: 'messages',
    props: ['type','id'],
    components: {
    },
    updated () {

      var labels = document.getElementsByClassName('messages-cell-view2')

      if (labels) {
        console.log(typeof labels)
        for (var i=0; i<labels.length; i++) {
          var label = labels[i]
          if (label.offsetHeight < label.scrollHeight) {

            var unwindLabels = document.getElementsByClassName('messages-cell-label'+i)
            if (unwindLabels) {
              var unwindLabel = unwindLabels[0]
              unwindLabel.style.visibility = 'visible'
            }
          } else {

          }
        }
      }
//      var label = this.$refs.cell_label1



//      this.$refs.cell_label1.innerHTML = 'tttttt'
//      this.$refs.cell_label2.innerHTML = 'tttttt2222'
    },
    mounted () {
      //  messages-cell-labelWrap messages-cell-labelNoWrap

      this.textTest = '留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内'
//      this.textTest = '????'
      var self = this
      var unionid = localStorage.getItem('user-unionid-cache')
      var data = {}
      data.type = this.type
      data.targetId = this.id
      data.pageSize = 5
      data.pageNow = 1
      data.platform = 2
      data.platformUser = unionid

      if (unionid) {
        api.getMessage(data,function (respond) {
          self.datas = respond.body.data.records
        })
      }



    },
    methods: {
      onwind(index) {

        var Labels = document.getElementsByClassName('messages-cell-view2'+index)
        if (Labels) {
          var label = Labels[0]
          label.className = 'messages-cell-view2 messages-cell-labelNoWrap'
        }

        var unwindLabels = document.getElementsByClassName('messages-cell-label'+index)
        if (unwindLabels) {
          var unwindLabel = unwindLabels[0]
          unwindLabel.style.visibility = 'hidden'
        }
      },
      likeEvent (id,msg) {
        var unionid = localStorage.getItem('user-unionid-cache')
        var self = this
        var data = {}
        data.likeId = id
        if (this.type == 0) {

        }
        data.source = 3
        data.platform = 1
        data.platformUser = unionid

        if (msg.isLike) {
          api.setUnlike(data,function (respond) {
            msg.isLike = false
          })
        } else {
          api.setLikeMassage(data,function (respond) {
            msg.isLike = true
          })
        }



      }
    },
    computed: {

    },
    data () {
      return {
        datas: undefined,
        labelRefNames: ['cell_label1','cell_label2'],
        textTest: '留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容留言内容'
      }
    }
  }
</script>


<style lang="scss">
  @import '../../assets/css/mixin';

  .messages-header {
    background-color: white;
    padding-top: pxToRem(40px);
    padding-left: pxToRem(26px);
    padding-bottom: pxToRem(20px);

    font-family: PingFangSC-Regular;
    font-size: pxToRem(12px);
    color:#544014;
  }

  .messages-cell {
    background-color: white;
  }

  .messages-cell-userIcon {
    width: pxToRem(30px);
    height: pxToRem(30px);
    border-radius: pxToRem(15px);

    margin-left: pxToRem(16px);
  }
  .messages-cell-userName {
    font-family: PingFangSC-Light;
    font-size: pxToRem(12px);
    color:#9b9b9b;
    text-align:left;
    margin-left: pxToRem(8px);
    text-overflow: ellipsis;
    overflow: hidden;
    white-space:nowrap;
    width: pxToRem(202px);

  }
  .messages-cell-time {
    font-family: PingFangSC-Light;
    font-size: pxToRem(11px);
    color:#9b9b9b;
    text-align:left;
    margin-right: pxToRem(22px);
  }
  .messages-cell-view1 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: pxToRem(20px);
  }

  .messages-cell-view2 {
    margin-top: pxToRem(10px);
    margin-bottom: pxToRem(20px);
    padding-left: pxToRem(51px);
    padding-right: pxToRem(25px);
    font-family: PingFangSC-Light;
    font-size:pxToRem(13px);
    color:#544014;
    text-align:left;

    /*height: pxToRem(54px);*/

  }
  .messages-cell-labelWrap {
    overflow : hidden; text-overflow: ellipsis; display: -webkit-box; -webkit-line-clamp: 3; -webkit-box-orient: vertical;
  }
  .messages-cell-labelNoWrap {

  }

  .messages-cell-view3 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: pxToRem(51px);
    padding-right: pxToRem(25px);
    padding-bottom: pxToRem(30px);
  }

  .messages-cell-iconView-label {
    font-family:PingFangSC-Light;
    font-size:pxToRem(12px);
    color:#9b9b9b;
    margin-left: pxToRem(7px);

  }
  .messages-cell-iconView-img {
    width: pxToRem(16px);
    height: pxToRem(17px);
  }

  .messages-cell-iconView1 {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-direction: row;
    height: pxToRem(22px);
  }

  .messages-cell-iconView {
    margin-left: pxToRem(18px);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    width: pxToRem(50px);
  }
  .messages-cell-iconView3 {
    width: pxToRem(35px);
  }

  .messages-cell-label {
    font-family: PingFangSC-Light;
    font-size:pxToRem(12px);
    color:#9b9b9b;
    text-align:left;
    visibility: hidden;
  }

</style>



