import Vue from 'vue'
import Router from 'vue-router'
import shareArticle from '../components/share/shareArticle.vue'
import shareCourse from '../components/share/shareCourse.vue'
Vue.use(Router)

export default new Router({
  // hashbang: true,
  // history: true,
  mode: 'history',
  routes: [
    {
      path: '/shareArticle/:id',
      name: 'shareArticle',
      component: shareArticle
    },
    {
      path: '/shareCourse/:id',
      name: 'shareCourse',
      component: shareCourse
    }
  ]
})



