import api from '../api'

export default {
  getArticleDetail (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.post('api/detail/6/'+data, {}, successCallback, errCallback, isShowErrMsg)
  },
  getCourseDetail (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.post('api/detail/11/'+data, {}, successCallback, errCallback, isShowErrMsg)
  },
  getMessage (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.post('api/ArticleLeaveMessage/getMessage', data, successCallback, errCallback, isShowErrMsg)
  },
  getUnionid (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.post('api/blissbakery-gateway-web/catchUnionid/'+data, {}, successCallback, errCallback, isShowErrMsg)
  },
  setLikeMassage (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.post('/lx/blissbakery-gateway-web/like', data, successCallback, errCallback, isShowErrMsg)
  }, //http://test.interface.blissbakery.com/api/unlike
  setUnlike (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.post('/lx/blissbakery-gateway-web/unlike', data, successCallback, errCallback, isShowErrMsg)
  }
}



