<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
  import './assets/js/flexible.js'
  export default {
    name: 'app',
    methods: {
      dealGetUserInfoErr ( isAttention, url) {

        if (window.location.origin.indexOf('wap.xfxb.net') === -1 ) {
          return
        }
        // 如果已经关注过公众号，则不处理
        if (isAttention && isAttention == true) {
          return
        }

        var lastTimeStr = localStorage.getItem('lastPopGetUserInfo-timeCache')
        // 如果最后弹出时间已经过了23小时，则跳转到弹出确认获取信息的openWX链接
        if (lastTimeStr) {

          var lastTime = new Date(lastTimeStr)
          lastTime.setTime(lastTime.getTime()+23*60*60*1000)
          var nowTime = new Date()
          if (nowTime > lastTime) {
            localStorage.setItem('lastPopGetUserInfo-timeCache',nowTime)
            window.location.href = url
          }

        } else { // 如果没有记录弹出时间（第一次）,则直接跳转
          var nowTime = new Date()
          localStorage.setItem('lastPopGetUserInfo-timeCache',nowTime)
          window.location.href = url
        }

      },
      dealWXSign () {

        var code = this.utils.getUrlKey('code')
        var stateVal = this.utils.getUrlKey('state')
        var unionid = localStorage.getItem('user-unionid-cache')
        var self = this
//        var origin = window.location.origin
        var origin = 'http://wap.xfxb.net/'
        var state = '&state=' + this.$route.fullPath.replace(/[\/]/g,'%2F')


        var getUserInfoUrl = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx1d101c2fc4fb041b&redirect_uri=' + origin + '&response_type=code&scope=snsapi_userinfo' + state + '#wechat_redirect'

        // 当链接地址里面有code的时候(从openWX跳转的页面)，自动进行微信登录
        if (code !== null) {

          var data = {}
          data.code = code

          api.wxAutoSign(data,function (respond) {

            localStorage.setItem('isWxLogin-cache', true)
            if (respond.body.data['userId'] !== undefined) {
              localStorage.setItem('userIsLogin', true)
            }

            self.utils.delParama('code')
            if (stateVal !== null && stateVal !== undefined && stateVal.length > 3) {
              var newState = stateVal.replace(/[%2F]/g,'/')
              self.$router.replace(newState)
            }

          },function (err) {
            self.utils.alert('登录失败，请尝试刷新页面')
            if (stateVal !== null && stateVal !== undefined && stateVal.length > 3) {
              var newState = stateVal.replace(/[%2F]/g,'/')
              self.$router.replace(newState)
            }
          },false)

        } else if ( unionid == null ) // 当链接地址没有code且在微信中打开时，判断是否跳转到openWX进行登录
        {
          window.location.href = url

        }
      },
    },
    created () {
      this.dealWXSign()
      console.log('v1.0.1')
    }
  }
</script>

<style src="./assets/css/reset.css" lang="css"></style>
<style src="./assets/css/public.scss" lang="scss"></style>
<style src="./assets/css/mint-ui-reset.scss" lang="scss"></style>
<style src="./assets/css/_mixin.scss" lang="scss"></style>
<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style>



